/* client/src/App.css */

@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,600,700,900');
@font-face {
    font-family: 'Source Sans Pro';
    src: url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,600,700,900');
    @import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,600,700,900') format('truetype');
}


body {
    background-color: black;
}

h1 {
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 900;
    font-size: 34px;
    color: white;
}

h3 {
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 900;
    font-size: 56px;
    color: white;
}

.h2 {
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 400;
    font-size: 22px;
    color: white;
}


p {
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 400;
    font-size: 14px;
    color: white;
}

hr {
    border: 0;
    height: 3px;
    background-color: white;
}

.hr-container {
    position: relative;
    top:-20px;
    width: 300px;
}

.hr-menu-info-divider {
    height: 10px;
    background-color: #ff2d00;
}

.header-section {
}

.orange-text {
    color: #ff2d00;
}

.orange-link:link, .orange-link:active, .orange-link:hover, .orange-link:visited {
    color: #ff2d00;
    text-decoration: none;

}

.flex-container {
    max-width:450px;
    display: block;
}

.icon-img {
    width: 95px;
    margin-right: 10px;
    margin-bottom: 10px;
}

.round-image {
    border-radius: 50%;
    width:95px;
    height:95px;
    margin-right: 10px;
    margin-bottom: 10px;
}

.artist-container {
    display: inline-block;
    position:relative;
}

.App {
    margin: 0 auto;
}
.container {
    margin: 0 auto;
    max-width: 600px;
    padding: 10px;
}

a:link, a:visited, a:active, a:hover {
    color:black;
}

.spotify-icon {
    display: block;
    margin: 0 auto;
    text-align: center;
}


